<template><section><h1>checkbox 多选框</h1>
<hr>
<p>多选通常用在任务板、文件选择等场景中。</p>
<h2>何时使用</h2>
<p>用在一组可选项中进行多项选择时。</p>
<h2>基本多选框</h2>
<p>最简单的用法。
需要设置v-model绑定checked</p>
<demo-box>
                    <div slot="demo"><html><head></head><body><p>你平时无聊时喜欢干啥子?</p>
<cat-checkbox v-model="checked1" label="看电视">看电视</cat-checkbox>
<cat-checkbox v-model="checked2" label="打游戏">打游戏</cat-checkbox>
<cat-checkbox v-model="checked3" label="听歌">听歌</cat-checkbox>
</body></html></div>
                    <div slot="source-code"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>你平时无聊时喜欢干啥子?<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">cat-checkbox</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checked1"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"看电视"</span>&gt;</span>看电视<span class="hljs-tag">&lt;/<span class="hljs-name">cat-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">cat-checkbox</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checked2"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"打游戏"</span>&gt;</span>打游戏<span class="hljs-tag">&lt;/<span class="hljs-name">cat-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">cat-checkbox</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checked3"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"听歌"</span>&gt;</span>听歌<span class="hljs-tag">&lt;/<span class="hljs-name">cat-checkbox</span>&gt;</span>
</code></pre>
</div></demo-box><h2>禁用状态</h2>
<p>disabled
不可用状态</p>
<demo-box>
                    <div slot="demo"><html><head></head><body><cat-checkbox v-model="checked4" :disabled="disabled1" label="hello" @change="handleChange1">
  {{disabled1 ? '禁用' : '不禁用'}}
</cat-checkbox>
<cat-button size="small" type="danger" @click="disabled1 = !disabled1">切换禁用</cat-button>
<cat-button size="small" type="primary" @click="checked4 = !checked4">切换选中</cat-button>
<p>我是回调函数回来的checkbox value的值：{{checkedTest}}</p>
</body></html></div>
                    <div slot="source-code"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">cat-checkbox</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checked4"</span> <span class="hljs-attr">:disabled</span>=<span class="hljs-string">"disabled1"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"hello"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"handleChange1"</span>&gt;</span>
  {{disabled1 ? '禁用' : '不禁用'}}
<span class="hljs-tag">&lt;/<span class="hljs-name">cat-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">cat-button</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"small"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"danger"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"disabled1 = !disabled1"</span>&gt;</span>切换禁用<span class="hljs-tag">&lt;/<span class="hljs-name">cat-button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">cat-button</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"small"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"checked4 = !checked4"</span>&gt;</span>切换选中<span class="hljs-tag">&lt;/<span class="hljs-name">cat-button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>我是回调函数回来的checkbox value的值：{{checkedTest}}<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
</code></pre>
</div></demo-box><h2>多选组合</h2>
<p>方便的从数组生成 Checkbox 组
disabled 以按钮组为主, 子项为辅， 如果整体是禁用， 那么子项也是被禁的</p>
<h4>第一种通过比较复杂的形式 可以控制选中 禁用等</h4>
<demo-box>
                    <div slot="demo"><html><head></head><body><p>你喜欢吃什么？</p>
<cat-checkbox-group v-model="lists1" :options="options1"></cat-checkbox-group>
<p>当前选中: <span v-for="item in lists1"> {{item}} </span></p>
</body></html></div>
                    <div slot="source-code"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>你喜欢吃什么？<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">cat-checkbox-group</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"lists1"</span> <span class="hljs-attr">:options</span>=<span class="hljs-string">"options1"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cat-checkbox-group</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>当前选中: <span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">v-for</span>=<span class="hljs-string">"item in lists1"</span>&gt;</span> {{item}} <span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
</code></pre>
<pre v-pre=""><code v-pre="" class="language-js">&lt;script&gt;
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">lists1</span>: [],
      <span class="hljs-attr">options1</span>: [
        {
          <span class="hljs-attr">label</span>: <span class="hljs-string">'冰淇淋'</span>,
          <span class="hljs-attr">text</span>: <span class="hljs-string">'冰淇淋'</span>,
          <span class="hljs-attr">checked</span>: <span class="hljs-literal">true</span>,
          <span class="hljs-attr">disabled</span>: <span class="hljs-literal">true</span>
        },
        {
          <span class="hljs-attr">label</span>: <span class="hljs-string">'周黑鸭'</span>,
          <span class="hljs-attr">text</span>: <span class="hljs-string">'周黑鸭'</span>,
          <span class="hljs-attr">checked</span>: <span class="hljs-literal">true</span>
        },
        {
          <span class="hljs-attr">label</span>: <span class="hljs-string">'巧克力'</span>,
          <span class="hljs-attr">text</span>: <span class="hljs-string">'巧克力'</span>,
          <span class="hljs-attr">disabled</span>: <span class="hljs-literal">true</span>
        },
        {
          <span class="hljs-attr">label</span>: <span class="hljs-string">'鸡排'</span>,
          <span class="hljs-attr">text</span>: <span class="hljs-string">'鸡排'</span>
        }
      ]
    }
  },
  <span class="hljs-attr">methods</span>: {
  }
}
&lt;<span class="hljs-regexp">/script&gt;
</span></code></pre>
</div></demo-box><h4>第二张  简单的通过两个数组 实现全选</h4>
<p>indeterminate 属性用以表示 checkbox 的不确定状态，一般用于实现全选的效果</p>
<demo-box>
                    <div slot="demo"><html><head></head><body><p>选择你想喝的饮料？</p>
<p><cat-checkbox v-model="checkedAll" :indeterminate="indeterminate" @change="handleChange2" label="checkAll">全选</cat-checkbox></p>
<cat-checkbox-group v-model="lists2" :options="options2" @change="handleChangeItem"></cat-checkbox-group>
<p>当前选中: <span v-for="item in lists2"> {{item}} </span></p>
</body></html></div>
                    <div slot="source-code"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>选择你想喝的饮料？<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">cat-checkbox</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checkedAll"</span> <span class="hljs-attr">:indeterminate</span>=<span class="hljs-string">"indeterminate"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"handleChange2"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"checkAll"</span>&gt;</span>全选<span class="hljs-tag">&lt;/<span class="hljs-name">cat-checkbox</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">cat-checkbox-group</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"lists2"</span> <span class="hljs-attr">:options</span>=<span class="hljs-string">"options2"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"handleChangeItem"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cat-checkbox-group</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>当前选中: <span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">v-for</span>=<span class="hljs-string">"item in lists2"</span>&gt;</span> {{item}} <span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
</code></pre>
<pre v-pre=""><code v-pre="" class="language-js">&lt;script&gt;
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">lists2</span>: [<span class="hljs-string">'雪碧'</span>],
      <span class="hljs-attr">options2</span>: [<span class="hljs-string">'加多宝'</span>, <span class="hljs-string">'雪碧'</span>, <span class="hljs-string">'可乐'</span>, <span class="hljs-string">'酸奶'</span>],
      <span class="hljs-attr">checkedAll</span>: <span class="hljs-literal">false</span>,
      <span class="hljs-attr">indeterminate</span>: <span class="hljs-literal">true</span>
    },
    <span class="hljs-attr">methods</span>: {
      handleChange2 (val) {
        <span class="hljs-keyword">this</span>.lists2 = <span class="hljs-keyword">this</span>.checkedAll ? [].concat(<span class="hljs-keyword">this</span>.options2) : []
        <span class="hljs-keyword">this</span>.indeterminate = <span class="hljs-literal">false</span>
      },
      handleChangeItem (val) {
        <span class="hljs-keyword">this</span>.checkedAll = (<span class="hljs-keyword">this</span>.lists2.length === <span class="hljs-keyword">this</span>.options2.length)
        <span class="hljs-keyword">this</span>.indeterminate = val.length &gt; <span class="hljs-number">0</span> &amp;&amp; val.length &lt; <span class="hljs-keyword">this</span>.options2.length
      }
    }
  },
  <span class="hljs-attr">methods</span>: {
  }
}
&lt;<span class="hljs-regexp">/script&gt;
</span></code></pre>
</div></demo-box><h2>checkbox 参数</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>label</td>
<td>checkbox的value</td>
<td><code v-pre="">string</code> , <code v-pre="">number</code> , <code v-pre="">boolean</code></td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>name</td>
<td>原生属性</td>
<td><code v-pre="">string</code></td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>disabled</td>
<td>禁止点击</td>
<td>Boolean</td>
<td><code v-pre="">true</code>, <code v-pre="">false</code></td>
<td>false</td>
</tr>
<tr>
<td>indeterminate</td>
<td>设置 indeterminate 状态, 全选部分选中的样式</td>
<td>Boolean</td>
<td><code v-pre="">true</code>, <code v-pre="">false</code></td>
<td>-</td>
</tr>
<tr>
<td>@change</td>
<td>回调函数</td>
<td>function</td>
<td>-</td>
<td>返回选中的checbox的value</td>
</tr>
</tbody>
</table>
<h2>checkboxGroup 参数</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>options</td>
<td>chackbox数据</td>
<td>Array</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>v-model</td>
<td>默认选中的子项列表</td>
<td>Array</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>disabled</td>
<td>禁止点击</td>
<td>Boolean</td>
<td><code v-pre="">true</code>, <code v-pre="">false</code></td>
<td>false</td>
</tr>
<tr>
<td>@change</td>
<td>回调函数</td>
<td>function</td>
<td>-</td>
<td>返回选中的checbox的数组</td>
</tr>
</tbody>
</table>
<h4>options说明</h4>
<p>子项：label- 对应value, text - 对应显示文字， disabled, checked代表是否选中，同时也可以是一个简单的数组, [label1, label2]</p>


</section></template>
<style lang="scss" scoped>
 p {
   padding: 16px 0;
 }
</style>
<script>
  export default {
    data () {
      return {
        checked1: false,
        checked2: false,
        checked3: false,
        checked4: false,
        checkedTest: '',
        disabled1: false,
        lists1: [],
        options1: [
          {
            label: '冰淇淋',
            text: '冰淇淋',
            checked: true,
            disabled: true
          },
          {
            label: '周黑鸭',
            text: '周黑鸭',
            checked: true
          },
          {
            label: '巧克力',
            text: '巧克力',
            disabled: true
          },
          {
            label: '鸡排',
            text: '鸡排'
          }
        ],
        lists2: ['雪碧'],
        options2: ['加多宝', '雪碧', '可乐', '酸奶'],
        checkedAll: false,
        indeterminate: true
      }
    },
    methods: {
      handleChange1 (val) {
        this.checkedTest = val
      },
      handleChange2 (val) {
        this.lists2 = this.checkedAll ? [].concat(this.options2) : []
        this.indeterminate = false
      },
      handleChangeItem (val) {
        this.checkedAll = (this.lists2.length === this.options2.length)
        this.indeterminate = val.length > 0 && val.length < this.options2.length
      }
    }
  }
</script>


